.todoItem {
  display: flex;
  width: 100%;
  min-height: 3.6rem;
  height: fit-content;
  margin: 1.5rem 0;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background-color: #2D2C2C;
  align-items: center;
  justify-content: space-between;
}
.todoItem_check {
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 0.2rem;
  border: solid 1px #6E6E6E;
  background-color: #202020;
}
.todoItem_check:hover {
  /* cursor: pointer; */
  box-shadow: 0px 0px 2px 1px rgb(250 250 250 / 50%);
}
.todoItem_delete:hover {
  color: #ed0000;
  cursor: pointer;
  font-stretch: expanded;
}
.todoItem_p__completed {
  text-decoration: line-through;
}
.todoItem_check__active {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2880d2;
  border: solid 1px #2880d2;
}
.todoItem_check__active::after{
  font-size: 1.2rem;
  content: 'âœ“';
}
.todoItem p {
  font-family: "Montserrat", sans-serif;
  width: 21rem;
  margin: 0;
  font-size: 1.6rem;
  font-weight: 200;
}
.todoItem_delete {
  font-size: 1.6rem;
}